    // 日期选项值发生变化时触发
    onDateChange() {
      // console.log(this.searchValidateForm.selectDateState);
      // 将对象转换成数组
      var arr = []
      for (let i in this.searchTableFields) {
        let o = {}
        o[i] = this.searchTableFields[i] //即添加了key值也赋了value值 o[i] 相当于o.name 此时i为变量
        arr.push(o)
      }

      if (this.searchValidateForm.selectDateState == '1') {
        // 按日查询
        this.dateType = 'daterange'

        // 如果“环比”字段不存在
        if (!this.searchTableFields.hasOwnProperty('环比')) {
          arr.forEach((item, index) => {
            if (Object.values(item)[0] == 'tb') {
              arr.splice(index + 1, 0, { 环比: 'hb' })
            }
          })
        }

        // 如果“目标金额”、“目标完成比例存在”，则删除
        if (
          !!this.searchTableFields.hasOwnProperty('目标金额') ||
          !!this.searchTableFields.hasOwnProperty('目标完成比例')
        ) {
          // 注意这里需要根据索引删除字段，所以倒着删就行了
          for (var i = arr.length - 1; i > 0; i--) {
            if (
              Object.values(arr[i])[0] == 'target' ||
              Object.values(arr[i])[0] == 'mbwcbl'
            ) {
              arr.splice(i, 1)
            }
          }
        }
      } else if (this.searchValidateForm.selectDateState == '2') {
        // 按月查询
        this.dateType = 'monthrange'

        // 如果“环比”字段不存在
        if (!this.searchTableFields.hasOwnProperty('环比')) {
          arr.forEach((item, index) => {
            if (Object.values(item)[0] == 'tb') {
              arr.splice(index + 1, 0, { 环比: 'hb' })
            }
          })
        }

        if (this.searchValidateForm.amountType == '1') {
          // 如果“目标金额”、“目标完成比例”不存在
          if (
            !this.searchTableFields.hasOwnProperty('目标金额') ||
            !this.searchTableFields.hasOwnProperty('目标完成比例')
          ) {
            arr.forEach((item, index) => {
              if (Object.values(item)[0] == 'wuyingshou') {
                arr.splice(index + 1, 0, { 目标完成比例: 'mbwcbl' })
                arr.splice(index + 1, 0, { 目标金额: 'target' })
              }
            })
          }
        }
      } else if (this.searchValidateForm.selectDateState == '3') {
        // 按年查询
        this.dateType = 'yearrange'

        // 如果“环比”字段存在
        if (!!this.searchTableFields.hasOwnProperty('环比')) {
          // 删除“环比”字段
          arr.forEach((item, index) => {
            if (Object.values(item)[0] == 'hb') {
              arr.splice(index, 1)
            }
          })
        }

        if (this.searchValidateForm.amountType == '1') {
          // 如果“目标金额”、“目标完成比例不存在”
          if (
            !this.searchTableFields.hasOwnProperty('目标金额') ||
            !this.searchTableFields.hasOwnProperty('目标完成比例')
          ) {
            arr.forEach((item, index) => {
              if (Object.values(item)[0] == 'wuyingshou') {
                arr.splice(index + 1, 0, { 目标完成比例: 'mbwcbl' })
                arr.splice(index + 1, 0, { 目标金额: 'target' })
              }
            })
          }
        }
      }

      // 将数组转换成对象
      var result = {}
      arr.forEach((item) => {
        result[Object.keys(item)[0]] = Object.values(item)[0]
      })
      console.log(result)
      this.searchTableFields = result
    },

    // 表格排序触发
    onSortChange({ column, prop, order }) {
      console.log(column, prop, order)
      if (order == 'ascending') {
        order = 'asc'
      } else if (order == 'descending') {
        order = 'desc'
      }

      if (!order) {
        this.order = null
      } else {
        this.order = `${prop} ${order}`
      }
      this.initTableData()
    },

