    /** 搜索表单中选项变化方法 start */
    // 若先选择公司名称，则关联显示可选择的景区名称
    async onChangeSearchCompany(companyId) {
      // console.log(companyId);
      var res = await this.getScenicList(companyId)
      console.log(res)
      var searchScenicOptions = res.data

      this.searchScenicOptions = searchScenicOptions

      // 如果返回的景区列表为空，则直接返回
      if (searchScenicOptions.length == 0) {
        // this.$message({
        //   type: "info",
        //   message: "/system/robot_time_statistics/scenicList 接口返回的景区列表为空"
        // })
        this.searchValidateForm.spotId = ''
        return
      }

      var isHasScenic = false
      searchScenicOptions.forEach((item) => {
        if (item.SCENIC_SPOT_ID == this.searchValidateForm.spotId) {
          isHasScenic = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则景区，则为搜索景区选择框设置默认值
      if (!!companyId && !isHasScenic) {
        this.searchValidateForm.spotId = searchScenicOptions[0].SCENIC_SPOT_ID
      }
    },

    // 景区选项变化时触发
    async onChangeSearchSpot(spotId) {
      var res = await this.getCompanyList(spotId)
      // console.log(res.data);
      var searchCompanyOptions = res.data

      this.searchCompanyOptions = searchCompanyOptions

      // 如果返回的公司列表为空，则直接返回
      if (searchCompanyOptions.length == 0) {
        // this.$message({
        //   type: "info",
        //   message: "/system/robot_time_statistics/companyList 接口返回的公司列表为空"
        // })
        this.searchValidateForm.companyId = ''
        return
      }

      var isHasCompany = false
      searchCompanyOptions.forEach((item) => {
        if (item.COMPANY_ID == this.searchValidateForm.companyId) {
          isHasCompany = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则公司，则为搜索公司选择框设置默认值
      if (!!spotId && !isHasCompany) {
        this.searchValidateForm.companyId = searchCompanyOptions[0].COMPANY_ID
      }
    },

    // 统计方式选项变化时触发
    onChangeType(type) {
      // console.log(type);

      // 将对象转换成数组
      var arr = []
      for (let i in this.searchTableFields) {
        let o = {}
        o[i] = this.searchTableFields[i] //即添加了key值也赋了value值 o[i] 相当于o.name 此时i为变量
        arr.push(o)
      }
      if (type == '1') {
        // 选择了按景区统计

        // 如果“机器人编号”字段存在
        if (!!this.searchTableFields.hasOwnProperty('机器人编号')) {
          // 删除“机器人编号”字段
          // 注意这里需要根据索引删除字段，所以倒着删就行了
          for (var i = arr.length - 1; i > 0; i--) {
            if (Object.values(arr[i])[0] == 'ROBOT_CODE') {
              arr.splice(i, 1)
            }
          }
        }

        // 启用统计报表、统计支付方式、金额类型统计选择框
        this.disabledStatGrossProfit = false
        this.disabledStatPayWay = false
        this.disabledAmountType = false
      } else if (type == '2') {
        // 选择了按机器人统计

        // // 如果“机器人型号”或者“机器人编号”字段不存在
        // if (!this.searchTableFields.hasOwnProperty("机器人型号") || !this.searchTableFields.hasOwnProperty("机器人编号")) {
        //   // 在arr中索引为 2 的位置插入对象
        //   arr.splice(2, 0, { "机器人型号": "机器人型号字段" })
        //   arr.splice(2, 0, { "机器人编号": "ROBOT_CODE" })
        // }

        // 如果“机器人编号”字段不存在
        if (!this.searchTableFields.hasOwnProperty('机器人编号')) {
          // 在arr中索引为 2 的位置插入对象
          arr.splice(2, 0, { 机器人编号: 'ROBOT_CODE' })
        }

        // 禁用统计报表，禁用统计支付方式，将金额类型选择框选择交易金额并禁用
        this.disabledStatGrossProfit = true
        this.disabledStatPayWay = true
        this.searchValidateForm.amountType = '1'
        // 显示交易金额和平均交易金额两个字段

        // 如果“交易金额/元”字段不存在
        if (!this.searchTableFields.hasOwnProperty('交易金额/元')) {
          // 添加“交易金额/元”字段
          // 在索引为 2 的位置删除 0 个元素，添加新的对象元素
          arr.splice(2, 0, { '交易金额/元': 'realAmount' })
        }
        this.disabledAmountType = true

        // 如果当前查询方式不是“按日查询”
        if (this.searchValidateForm.selectDateState != '1') {
          // 按日查询
          // 如果“目标金额”、“目标完成比例”不存在
          if (
            !this.searchTableFields.hasOwnProperty('目标金额') ||
            !this.searchTableFields.hasOwnProperty('目标完成比例')
          ) {
            arr.forEach((item, index) => {
              if (Object.values(item)[0] == 'wuyingshou') {
                arr.splice(index + 1, 0, { 目标完成比例: 'mbwcbl' })
                arr.splice(index + 1, 0, { 目标金额: 'target' })
              }
            })
          }
        }
      }

      // 将数组转换成对象
      var result = {}
      arr.forEach((item) => {
        result[Object.keys(item)[0]] = Object.values(item)[0]
      })
      this.searchTableFields = result
    },

    // 金额类型选项变化时触发
    onChangeAmountType(amountType) {
      // console.log(amountType);

      // 将对象转换成数组
      var arr = []
      for (let i in this.searchTableFields) {
        let o = {}
        o[i] = this.searchTableFields[i] //即添加了key值也赋了value值 o[i] 相当于o.name 此时i为变量
        arr.push(o)
      }

      if (amountType == '1') {
        // 选择了交易金额
        // 显示交易金额和平均交易金额两个字段

        // 如果“交易金额/元”、“目标金额”、“目标完成比例”字段不存在
        if (!this.searchTableFields.hasOwnProperty('交易金额/元')) {
          // 添加“运营时长/小时”字段
          // 在索引为 2 的位置删除 0 个元素，添加新的对象元素
          arr.splice(2, 0, { '交易金额/元': 'realAmount' })
        }

        // 如果当前查询方式不是“按日查询”
        if (this.searchValidateForm.selectDateState != '1') {
          // 按日查询
          // 如果“目标金额”、“目标完成比例”不存在
          if (
            !this.searchTableFields.hasOwnProperty('目标金额') ||
            !this.searchTableFields.hasOwnProperty('目标完成比例')
          ) {
            arr.forEach((item, index) => {
              if (Object.values(item)[0] == 'wuyingshou') {
                arr.splice(index + 1, 0, { 目标完成比例: 'mbwcbl' })
                arr.splice(index + 1, 0, { 目标金额: 'target' })
              }
            })
          }
        }
      } else if (amountType == '2') {
        // 选择了平均运营时长
        // 只显示平均运营时长

        // 如果“交易金额/元”字段存在
        if (!!this.searchTableFields.hasOwnProperty('交易金额/元')) {
          // 删除“交易金额/元”字段
          arr.forEach((item, index) => {
            if (Object.values(item)[0] == 'realAmount') {
              arr.splice(index, 1)
            }
          })
        }

        // 如果“目标金额”、“目标完成比例存在”，则删除
        if (
          !!this.searchTableFields.hasOwnProperty('目标金额') ||
          !!this.searchTableFields.hasOwnProperty('目标完成比例')
        ) {
          // 注意这里需要根据索引删除字段，所以倒着删就行了
          for (var i = arr.length - 1; i > 0; i--) {
            if (
              Object.values(arr[i])[0] == 'target' ||
              Object.values(arr[i])[0] == 'mbwcbl'
            ) {
              arr.splice(i, 1)
            }
          }
        }
      }
      // 将数组转换成对象
      var result = {}
      arr.forEach((item) => {
        result[Object.keys(item)[0]] = Object.values(item)[0]
      })
      this.searchTableFields = result
    },

    /** 搜索表单中选项变化方法 end */

    /** 提交表单中选项变化方法 start */
    // 选择公司变化触发(通过name获取id并赋值给validateForm表单对象)
    async onChangeCompany(spotName) {
      // console.log(spotName);
      let obj = {}
      obj = this.companyOptions.find((item) => {
        //这里的companyOptions就是上面遍历的数据源
        return item.COMPANY_NAME === spotName //筛选出匹配数据，这里的COMPANY_NAME是你遍历数组每一项的label
      })
      var companyId = obj.COMPANY_ID //  这里的COMPANY_ID就是对应value的
      this.validateForm.companyId = companyId // 赋值给需要传给后台的表单验证对象

      // var res = await this.getScenicList(companyId)
      // var data = res.data
      // // 数据处理
      // var scenicOptions = data.map((item) => {
      //   return {
      //     SCENIC_SPOT_NAME: item.SPOT_NAME,
      //     SCENIC_SPOT_ID: item.SPOT_ID
      //   }
      // })

      // this.scenicOptions = scenicOptions
    },

    // 选择景区变化触发
    async onChangeSpot(spotName) {
      // console.log(spotName);
      let obj = {}
      obj = this.scenicOptions.find((item) => {
        //这里的scenicOptions就是上面遍历的数据源
        return item.SCENIC_SPOT_NAME === spotName //筛选出匹配数据，这里的SCENIC_SPOT_NAME是你遍历数组每一项的label
      })
      var spotId = obj.SCENIC_SPOT_ID //  这里的SCENIC_SPOT_NAME就是对应value的
      this.validateForm.spotId = spotId // 赋值给需要传给后台的表单验证对象

      // var res = await this.getCompanyList(spotId)
      // this.companyOptions = res.data
    },

    // 选择景区合作主体变化触发
    onChangeSubject(checkDate) {
      // console.log(checkDate);
      let obj = {}
      obj = this.subjectOptions.find((item) => {
        //这里的scenicOptions就是上面遍历的数据源
        return item.COMPANY_NAME === checkDate //筛选出匹配数据，这里的COMPANY_NAME是你遍历数组每一项的label
      })
      console.log(obj.COMPANY_ID) //  这里的COMPANY_ID就是对应value的
      this.validateForm.subjectId = obj.COMPANY_ID // 赋值给需要传给后台的表单验证对象
    },
    /** 提交表单中选项变化方法 end */

    // 重置
    onReset() {
      // 清空所有输入框内容
      this.searchValidateForm.companyId = ''
      this.searchValidateForm.spotId = ''
      this.searchValidateForm.type = '1'
      this.searchValidateForm.status = '1'
      // this.searchValidateForm.startEndDate = [
      //   getYesterday(),
      //   getYesterday()
      // ]
      this.searchValidateForm.startEndDate = [new Date(), new Date()]
      this.searchValidateForm.amountType = '1'
      this.searchValidateForm.realAmount = ''
      this.searchValidateForm.robotCode = ''

      this.searchValidateForm.selectDateState = '1'
      this.dateType = 'daterange'

      this.disabledStatGrossProfit = false
      this.disabledStatPayWay = false
      this.disabledAmountType = false

      // 重置列
      this.searchTableFields = {
        //导出Excel表格的表头设置
        起止时间: 'checkDate',
        景区名称: 'spotName',
        '交易金额/元': 'realAmount',
        同比: 'tb',
        环比: 'hb',
        '平均单台交易金额/元': 'avgsc',
        '运营时长/小时': 'yysc',
        '平均单台运营时长/小时': 'avgyysc',
        在线订单数: 'jxz',
        无营收天数: 'wuyingshou'
        // "目标金额": "target",
        // "目标完成比例": "mbwcbl",
      }

      // 重新获取表格数据
      this.initTableData()
      this.initEchartsData()
    },

    // 统计报表(显示柱状图)
    onStatGrossProfit(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // console.log(this.searchValidateForm);
          // echarts loading加载

          // 当前选择的开始时间
          var startDate = formatDateTime(
            this.searchValidateForm.startEndDate[0],
            this.searchValidateForm.selectDateState
          )
          // 当前选择的结束时间
          var endDate = formatDateTime(
            this.searchValidateForm.startEndDate[1],
            this.searchValidateForm.selectDateState
          )
          // 当前时间
          var currentDate = formatDateTime(
            new Date(),
            this.searchValidateForm.selectDateState
          )

          // 当前是按日统计、按月统计还是按年统计
          var type = ''
          switch (this.searchValidateForm.selectDateState) {
            case '1':
              type = '日'
              break
            case '2':
              type = '月'
              break
            case '3':
              type = '年'
              break
            default:
              break
          }

          // 如果开始时间等于当前时间或者结束时间等于当前时间
          if (startDate == currentDate || endDate == currentDate) {
            this.$confirm(
              `因当${type}未结束，统计当${type}的同比、环比数据不准确，是否继续?`,
              '提示',
              {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }
            )
              .then(() => {
                this.showTable = false
                this.showPieChart = false
                this.showBarChart = true
                this.initEchartsData()
              })
              .catch(() => {})
          } else {
            this.showTable = false
            this.showPieChart = false
            this.showBarChart = true
            this.initEchartsData()
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 统计支付方式
    onStatPayWay(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.showTable = false
          this.showBarChart = false
          this.showPieChart = true

          // console.log(this.searchValidateForm);
          // echarts loading加载
          // this.initEchartsData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 导出报表（导出柱状图）
    onExportReport() {
      this.$nextTick(() => {
        var exportReportEle = document.getElementById('exportReport')
        exportReportEle.href = this.myChart.getDataURL()
      })
    },

    // 点击查询数据按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // console.log(this.searchValidateForm);

          // 当前选择的开始时间
          var startDate = formatDateTime(
            this.searchValidateForm.startEndDate[0],
            this.searchValidateForm.selectDateState
          )
          // 当前选择的结束时间
          var endDate = formatDateTime(
            this.searchValidateForm.startEndDate[1],
            this.searchValidateForm.selectDateState
          )
          // 当前时间
          var currentDate = formatDateTime(
            new Date(),
            this.searchValidateForm.selectDateState
          )

          // 当前是按日统计、按月统计还是按年统计
          var type = ''
          switch (this.searchValidateForm.selectDateState) {
            case '1':
              type = '日'
              break
            case '2':
              type = '月'
              break
            case '3':
              type = '年'
              break
            default:
              break
          }

          // 如果开始时间等于当前时间或者结束时间等于当前时间
          if (startDate == currentDate || endDate == currentDate) {
            this.$confirm(
              `因当${type}未结束，统计当${type}的同比、环比数据不准确，是否继续?`,
              '提示',
              {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }
            )
              .then(() => {
                this.showBarChart = false
                this.showPieChart = false
                this.showTable = true
                this.initTableData()
              })
              .catch(() => {})
          } else {
            this.showBarChart = false
            this.showPieChart = false
            this.showTable = true
            this.initTableData()
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 导出Excel表格(点击导出数据按钮触发)
    exportExcel() {
      this.$confirm('确认导出Excel表格?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          this.downloadLoading = true

          await this.getExportTableData()
          // 使用nextTick当table表格渲染完成后执行导出表格操作
          this.$nextTick(() => {
            var xlsxParam = { raw: true } //转换成excel时，使用原始的格式
            /* 从表生成工作簿对象 */
            var wb = XLSX.utils.table_to_book(
              document.querySelector('#outTable'),
              xlsxParam
            )
            /* 获取二进制字符串作为输出 */
            var wbout = XLSX.write(wb, {
              bookType: 'xlsx',
              bookSST: true,
              type: 'array'
            })
            try {
              FileSaver.saveAs(
                //Blob 对象表示一个不可变、原始数据的类文件对象。
                //Blob 表示的不一定是JavaScript原生格式的数据。
                //File 接口基于Blob，继承了 blob 的功能并将其扩展使其支持用户系统上的文件。
                //返回一个新创建的 Blob 对象，其内容由参数中给定的数组串联组成。
                new Blob([wbout], {
                  type: 'application/octet-stream'
                }),
                //设置导出文件名称
                '按景区统计运营流水' + '.xlsx'
              )
            } catch (e) {
              if (typeof console !== 'undefined') console.log(e, wbout)
            }
            this.downloadLoading = false
            return wbout
          })

          this.$message({
            type: 'success',
            message: '已成功导出Excel表格！',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消导出Excel表格',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // 新增
    onAdd() {
      this.dialogVisible = true
      this.dialogState = 'add'
    },

    // 点击编辑时触发
    editRow(index, row) {
      console.log(index, row)
      this.dialogVisible = true // 是否显示编辑dialog对话框

      var rowObj = Object.assign({}, row) // 深拷贝row对象
      this.validateForm = rowObj
      this.dialogState = 'edit'
    },

    // dialog取消
    dialogClose() {
      this.dialogVisible = false
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log(this.validateForm)
          if (this.dialogState == 'add') {
            // 新增公司信息
            console.log('新增公司信息')
          } else if (this.dialogState == 'edit') {
            // 编辑公司信息
            console.log('编辑公司信息')
          }
          this.dialogVisible = false
          this.$refs.validateForm.resetFields() // 重置表单
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    /**
     * 自定义合计行
     */
    getSummaries(param) {
      const { columns, data } = param
      const sums = []
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = '合计'
          return
        }
        const values = data.map((item) => Number(item[column.property]))
        if (!values.every((value) => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr)
            if (!isNaN(value)) {
              return prev + curr
            } else {
              return prev
            }
          }, 0)
          sums[index] = ''
        } else {
          // sums[index] = '';
        }

        // console.log(column);
        if (column.property == 'realAmount') {
          sums[index] = this.totalRealAmount + '元'
        }

        if (column.property == 'yysc') {
          sums[index] = this.totalYysc + '小时'
        }

        // if (column.property == "wuyingshou") {
        //   sums[index] = this.totalWuyingshou + "天"
        // }
      })

      return sums
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData(true)
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData(true)
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
  },
  computed: {
    activeFixed() {
      return (value) => {
        // // 冻结左侧“合作主体、合作公司、收款单位、景区名称、月份”
        // if (value == "checkDate" || value == "spotName" || value == "payee" || value == "spotName" || value == "month") {
        //   return "left"
        // }
      }
    },

    tableContentValue() {
      return (row, value) => {
        var result = ''
        // 如果当前列为“同比、环比”并且值不为空，则在数字后面加上百分号
        if (value == 'tb' || value == 'hb' || value == 'mbwcbl') {
          if (!row[value]) {
            result = 0 + '%'
          } else {
            result = row[value] + '%'
          }
        } else {
          result = row[value]
        }
        return result
      }
    },

    sortable() {
      return (value) => {
        var result = ''
        if (
          value == 'spotName' ||
          value == '机器人型号字段' ||
          value == '运营模式字段'
        ) {
          result = false
        } else {
          result = 'custom'
        }
        return result
      }
    }
  }
}
</script>

